import * as React from "react"
import { HeadFC, Link, PageProps } from "gatsby"
import AppContent from "../components/AppContent"
import Head_ from "../components/Head_"

const NotFoundPage: React.FC<PageProps> = () => {
  const [hitokoto,setHitokoto] = React.useState({text:'',to:''})
  fetch('https://v1.hitokoto.cn')
  .then(response => response.json())
  .then(data => {
    setHitokoto({to:`https://hitokoto.cn/?uuid=${data.uuid}`,text:data.hitokoto})
  })
  .catch(console.error)
  return (
    <AppContent setTheme={undefined}>
      <div className="text-4xl text-center dark:text-gray-50">ç©ºé¡µé¢</div>
      <div className="text-xl text-center dark:text-gray-50">{hitokoto.text}&nbsp;&nbsp;&nbsp;<Link className="underline" to={hitokoto.to}>æºäºhitokoto</Link></div>
      <div className="text-center"><Link className="underline dark:text-gray-50 text-xl" to='/'>å›åˆ°é¦–é¡µ</Link></div>
    </AppContent>
  )
}

export default NotFoundPage

export const Head: HeadFC = () => <><title>ç©ºé¡µé¢</title><Head_ /><script dangerouslySetInnerHTML={{__html:`if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}`}}></script></>
